export default {
	name: 'toolbar',
	props: {
		showToolbar: {
			type: Boolean,
			default: true
		},
		cancelButtonText: {
			type: String,
			default: ''
		},
		title: {
			type: String,
			default: ''
		},
		confirmButtonText: {
			type: String,
			default: ''
		}
	},
	methods:{
		emit(event) {
			const { type } = event.currentTarget.dataset;
			this.$emit(type,type);
		},
	}
}
